import { AOS } from 'aos';
import React, { useEffect } from 'react';
import '../../assests/css/OddEvenImagesContent.css';

const OddEvenImagesContent = (props) => {
    useEffect(() => {
        setTimeout(() => {
          AOS.init();
        }, 1000);
      }, [])
    
    return (
        <div className='OddEvenImagesContent'>
            <div className='container'>
                {props.content.map((item, index) => {
                    console.log(item?.items_lists, "odd even page")
                    return (
                        <div key={index} className='row'>
                            {
                                (index % 2 != 0) ?
                                    <div className='row my-res-odd-even'>
                                        <div className='col-lg-6'>
                                            <img data-aos="fade-left" src={item.image} alt="recevers content" className='w-100' />
                                        </div>
                                        <div className='col-lg-6 pl-5'>
                                            
                                            <p className='heading mb-4'>{item.heading}</p>
                                            <p className='text'>{item.desc}</p>
                                            {item?.items_lists?.map((list_item, i) => {
                                                return (<div className='listing_main_div' data-aos="flip-down" key={i}>
                                                    <p className='number'>0{i + 1}</p>
                                                    <p className='text'>{list_item.text}</p>
                                                </div>
                                                )
                                            })}
                                        </div>
                                    </div>
                                    :
                                    <div className='row my-res-odd-even'>
                                        <div className='col-lg-6 pr-5'>
                                            <p className='heading mb-4'>{item.heading}</p>
                                            <p className='text'>{item.desc}</p>
                                            {item?.items_lists?.map((list_item, i) => {
                                                return (<div className='listing_main_div' data-aos="flip-up" key={i}>
                                                    <p className='number'>0{i + 1}</p>
                                                    <p className='text'>{list_item.text}</p>
                                                </div>
                                                )
                                            })}
                                        </div>
                                        <div className='col-lg-6'>
                                            <img data-aos="fade-right" src={item.image} alt="recevers content" className='w-100' />
                                        </div>
                                    </div>
                            }
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

export default OddEvenImagesContent;