import React from "react";
import logo from "../../assests/images/logo.png";
import logo_black from "../../assests/images/logo_black.png";
import "../../assests/css/Header.css";
import { useNavigate } from "react-router-dom";

const Header = (props) => {
  let navigate = useNavigate();

  return (
    <div className="header">
      <div className="container">
        <div className="row">
          <div className="col-lg-12">
            <div className="header_full">
              <div className="mr-5">
                <a href="/">
                  <img
                    className="header_logo"
                    src={props.black == true ? logo_black : logo}
                    alt="main logo"
                  />
                </a>
              </div>
              <div
                className={
                  props?.black == true ? "header_links_black " : "header_links"
                }
              >
                <div className="hdr_content_spilt">
                  <span
                    onClick={() => {
                      navigate("/");
                    }}
                    className="mb-2"
                  >
                    Home
                  </span>
                  <span
                    onClick={() => {
                      navigate("/aboutus");
                    }}
                  >
                    About
                  </span>
                </div>
                <div className="hdr_content_spilt">
                  <span
                    onClick={() => {
                      navigate("/community");
                    }}
                    className="mb-2"
                  >
                    Services
                  </span>
                  <span
                    onClick={() => {
                      navigate("/lifebelowwater");
                    }}
                  >
                    Blogs
                  </span>
                </div>
                <div className="hdr_content_spilt">
                  <span
                    onClick={() => {
                      navigate("/faqs");
                    }}
                    className="mb-2"
                  >
                    Faqs
                  </span>
                  <span
                    onClick={() => {
                      navigate("/givers");
                    }}
                  >
                    Givers
                  </span>
                </div>
                <div className="hdr_content_spilt">
                  <span
                    onClick={() => {
                      navigate("/community-content");
                    }}
                    className="mb-2"
                  >
                    Community Content
                  </span>
                  <span
                    onClick={() => {
                      navigate("/blog-categories");
                    }}
                  >
                    Blog Category
                  </span>
                </div>

                <div className="hdr_content_spilt">
                  <span
                    onClick={() => {
                      navigate("/partnership");
                    }}
                    className="mb-2"
                  >
                    Partner Ship
                  </span>
                  <span
                    onClick={() => {
                      navigate("/recievers");
                    }}
                  >
                    Recievers
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
