import React from 'react';

const WeWouldLike = () => {
    return (
        <div className='WeWouldLike'>
            <div className='container'>
                <div className='row'>
                    <div className='col-lg-4 align-center'>
                        <p  data-aos="fade-left" className='heading'>We Would <br /> like You <br /> to <br /> Invest </p>
                    </div>
                    <div className='col-lg-4'>
                        <p  data-aos="fade-right" className='label'>First Name</p>
                        <input  data-aos="fade-left" className='inpt-fld'/>
                        <p data-aos="fade-right" className='label'>Last Name</p>
                        <input  data-aos="fade-left" className='inpt-fld'/>
                        <p data-aos="fade-right" className='label'>Email</p>
                        <input  data-aos="fade-left" className='inpt-fld'/>
                        <p  data-aos="fade-right" className='label'>Phone Number</p>
                        <input  data-aos="fade-left" className='inpt-fld'/>
                    </div>  
                    <div className='col-lg-4'>
                        <textarea  data-aos="fade-right" placeholder='Message' className='inpt-txtarea'></textarea>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default WeWouldLike;