import React from 'react';
import '../../assests/css/Footer.css';
import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';
import { Link } from 'react-router-dom';

const Footer = () => {
    return (
        <div className='footer'>
            <div className='container'>
                <div className='row'>
                    <div className='col-lg-6'>
                        <div className='text_area'>
                            <p className='sign_up_stay'>Sign Up to Stay upto date  on  global innovation</p>
                            <button className='btn btn-light bt-learn-more'>Learn More <ArrowRightAltIcon className=' right_icon_learn_more' /></button>
                        </div>
                    </div>
                    <div className='col-lg-3'>
                        <p className='footer_pages_text'><Link to="/blog-categoriesOur"> Blogs</Link></p>
                        <p className='footer_pages_text'>Linkedin</p>
                        <p className='footer_pages_text'>Facebook</p>
                        <p className='footer_pages_text'>Instagram</p>
                        <p className='footer_pages_text'>Twitter</p>
                        <p className='footer_pages_text'>  <Link to="/space-sustainibility">Space Sustainibility</Link></p>
                        
                        <div className='light_text_div'>
                            <p className='footer_pages_down_text mb-4'><Link to="/legallink">Legal Links</Link></p>
                            <p className='footer_pages_down_text mt-4'>©2021 Benefacto.  All rights reserved</p>
                        </div>
                    </div>
                    <div className='col-lg-3'>
                    <p className='footer_pages_text'><Link to="/savenature">Save Nature</Link></p>
                        <p className='footer_pages_text'> <Link to="/savespace">Save Space</Link></p>
                        <p className='footer_pages_text'>  <Link to="/terms">Terms</Link></p>
                        <p className='footer_pages_text'>  <Link to="/howstarted">How Started</Link></p>
                        <p className='footer_pages_text'>  <Link to="/cryptotoken">cryptotoken</Link></p>
                        <p className='footer_pages_text'><Link to="/recievers-content"> Recievers Content</Link></p>
                    </div>

                </div>

            </div>
        </div>
    )
}

export default Footer;