import React from 'react';

const BenefitsPartnership = (props) => {
    return (
        <div className='BenefitsPartnership '>
            <div className='container'>
                <div className='row'>
                    <div className='col-lg-12 mb-rr text-center'>
                        <p className='main_head '>Benefits Of being part of Benefacto</p>
                    </div>
                    {props.content.map((item, index) => {
                        return (
                            <div key={index} className='col-lg-4'>
                                <div className='gry_div'>
                                    <img className='imggg mb-3' src={item.image} alt="dollar" />
                                    <p className='txt mb-4'>{item.desc}</p>
                                    <p className='heading_brdr'>{item.heading}</p>
                                </div>
                            </div>
                        )
                    })}

                </div>

            </div>
        </div>
    )
}

export default BenefitsPartnership;